import Web3 from 'web3'
import {LPAbi} from '../Abi/LP';
import {LPAbiA} from '../Abi/LPA';
import {AbiPair} from '../Abi/AbiPair';
import {ERC20} from '../Abi/ERC20';

const web3 = new Web3(Web3.givenProvider);

const getContracts = {

    DGNZContract: function(){
        return new web3.eth.Contract(LPAbi, "0x8f83b6E14EE0C3218Bea11c08954137848afD6c8")
    },

    ADGNZContract: function(){
        return new web3.eth.Contract(LPAbiA, "0x6d1126fCD69463bcee076531EE1d8fFFD94138a5")
    },

    PairContract: function(lp){
        return new web3.eth.Contract(AbiPair, lp)
    },
    tokenContract: function(tAddr){
         return new web3.eth.Contract(ERC20, tAddr) 
   },

}

export {web3, getContracts}
